/* Общие стили для всех кнопок */
.Button, .ButtonReset, .ButtonErrorDecrease, .ButtonErrorIncrease {
    width: 30px;
    height: auto;
    margin: 5px;
    padding: 5px;
    border-radius: 5px;
    font-size: 20px;
}

/* Стили для обычных кнопок */
.Button {
    color: #394b56;
    background-color: #C0D6DF;
    border: 2px solid #394b56;
}

.ButtonReset {
    color: #394b56;
    background-color: #88A2AD;
    width: 100%;
}

/* Стили для кнопок в состоянии disabled */
.Button:disabled, .ButtonErrorDecrease:disabled, .ButtonErrorIncrease:disabled {
    /*width: 30px; !* Уменьшение ширины в состоянии disabled *!*/
    /*font-size: 20px; !* Уменьшение размера шрифта в состоянии disabled *!*/
    opacity: 0.5; /* Прозрачность в состоянии disabled */
}



