.App {
    margin: 0 auto;
    box-sizing: border-box;
}

.error {
    color: orangered;
}

.container {
    max-width: 350px;
    width: 100%;
    margin: 0 auto;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #DBE9EE;
    border: 2px solid #93ACCA;
    border-radius: 10px;
}

.setting-container {
    width: 80%;
    /*height: auto;*/
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background-color: #DBE9EE;
    border: 2px solid #93ACCA;
    border-radius: 5px;
    margin: 20px;
}

.counter {
    width: 80%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #DBE9EE;
    border: 2px solid #93ACCA;
    border-radius: 5px;
    margin-bottom: 20px;
}

.wrapper {
    display: flex;

}





